import React, { Component } from 'react'

export class NewsItem extends Component {
  render() {
    let {title,description,imageurl,newsurl,author,date,source}=this.props;
    return (
      <div className="my-3">
                <div className="card">
                <span className="position-absolute top-0 translate-middle badge rounded-pill bg-danger" style={{left:'90%',zIndex:'1'}}>{source}</span>
                <img src={!imageurl?'https://cdn.theathletic.com/app/uploads/2023/04/28003824/USATSI_20552160-1024x717.jpg':imageurl}  className="card-img-top" alt="..."/>
                <div className="card-body">
                <h5 className="card-title">{title}
                </h5>
              
                <p className="card-text">{description}</p>
                <p className="card-text"><small className="text-muted">By : {author} On {new Date(date).toLocaleString()}</small></p>
                <a href={newsurl} className="btn btn-sm btn-dark">Read more</a>
                </div>
                </div>
      </div>
    )
  }
}

export default NewsItem
